.shop-sidebar {
  & h4 {
    font-size: 18px;
    font-weight: 600;
    color: black;
    margin-bottom: 25px;
    padding-bottom: 15px;
    position: relative;
    text-transform: uppercase;

    &::before {
      position: absolute;
      content: "";
      background: #FF5151FF;
      width: 50px;
      height: 2px;
      left: 0;
      bottom: -1px;
    }
  }

  & ul {
    padding-left: 0 !important;
    background-color: #ffffff;
    line-height: 23px;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    visibility: visible;
    font-family: "Poppins", sans-serif;
    color: #222;
    position: relative;
    left: 0;

    & > li {
      display: block;
      margin-bottom: 10px;
      padding-left: 0 !important;

      &:last-child {
        margin-bottom: 0;
      }

      & > a {
        font-size: 15px;
        display: block;
        font-weight: 500;
        color: #222;
        text-decoration: none;
        padding-left: 0 !important;

        & span {
          float: right;
        }
      }
    }
  }
}

