.NavbarCom {
  box-shadow: 1px 5px 15px 1px #000000;

  .logo_nav {
    width: 150px;
    //font-family: 'Righteous', cursive;
    //font-size: 40px;
    //color: rgb(63, 94, 251) !important;
  }

  .nav_div {
    margin: auto 230px;

    .nav_text {
      width: 100% !important;
      padding: 0 20px !important;
      color: rgb(63, 94, 251) !important;
      font-family: 'Righteous', cursive;
      font-size: 18px;

      a {
        text-decoration: none;
        list-style: none;
      }
    }
  }

  .navbar li {
    position: relative;
  }

  .navbar > ul > li {
    white-space: nowrap;
    padding: 10px 0 10px 28px;
  }

  .navbar a, .navbar a:focus {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 3px;
    font-size: 15px;
    font-weight: 600;
    color: blue;
    white-space: nowrap;
    transition: 0.3s;
    position: relative;
  }

  .navbar a i, .navbar a:focus i {
    font-size: 12px;
    line-height: 0;
    margin-left: 5px;
  }

  .navbar > ul > li > a:before {
    content: "";
    position: absolute;
    height: 2px;
    bottom: -6px;
    left: 0;
    background-color: #106eea;
    visibility: hidden;
    transition: all 0.3s ease-in-out 0s;
  }

  .navbar a:hover:before, .navbar li:hover > a:before, .navbar .active:before {
    visibility: visible;
    width: 100%;
  }

  .navbar a:hover, .navbar .active, .navbar .active:focus, .navbar li:hover > a {
    color: #106eea;
  }

  ul {
    margin: auto 160px;
  }

  ul li {
    text-decoration: none;
    list-style: none;
    padding: 20px;

    a {
      text-decoration: none;

    }
  }

  .nav_icon1 {
    display: none;
  }

  .nav_icon {
    .nav_log {
      color: rgb(63, 94, 251);
    }

    .img_logo {
      width: 25px;
      height: 25px;
      margin: auto 18px;
      color: rgb(63, 94, 251);
    }
  }

  .from_nav {
    display: flex;
    position: absolute;
    top: 130%;
    right: 18%;

    .search_nav:hover > .nav_logo {
      display: none;

    }
  }

  .nav_logo:hover > .search_nav {
    display: flex;
    width: 200px;
    height: 16px;
  }


  @media (max-width: 1200px) and (min-width: 1024px) {
    .logo_nav {
      //font-size: 30px !important;
      width: 100px;
    }
    .nav_icon1 {
      display: none;
    }
    .nav_div {
      margin: auto 130px !important;

      .nav_text {
        padding: 0 20px !important;
        color: rgb(63, 94, 251) !important;
        font-size: 16px;
      }
    }

    .nav_icon {
      .img_logo {
        width: 20px;
        height: 20px;
        margin: 0 10px 7px;
      }
    }
  }

  @media (max-width: 1024px) and (min-width: 768px) {
    .logo_nav {
      width: 80px;
      //font-size: 25px !important;
    }
    .nav_div {
      text-align: center;
    }
    .nav_icon1 {
      display: flex;
      margin-left: 40%;

      .img_logo {
        width: 25px;
        height: 25px;
        margin: auto 10px 10px;
      }
    }
    .nav_div {
      .nav__text {
        text-align: center;
        width: 100%;
      }

      .nav_text {
        text-align: center;
        padding: 0 20px !important;
        color: rgb(63, 94, 251) !important;
        font-size: 16px;
      }
    }

    .nav_icon {
      display: none;

    }
  }
  @media (max-width: 767px) and (min-width: 357px) {
    .logo_nav {
      //font-size: 20px !important;
      width: 60px;
    }
    .nav_icon1 {
      display: flex;
      margin-left: 4%;

      .img_logo {
        width: 18px;
        height: 18px;
        margin: auto 8px 12px;
      }
    }
    .nav_div {
      margin: auto 150px;
      text-align: center;
    }
    .nav_div {
      display: block;
      justify-content: left;
      align-items: center;

      //.nav_text {
      //  text-align: start;
      //  padding: 0 30px 0 0 !important;
      //  color: rgb(63, 94, 251) !important;
      //  font-size: 18px;
      //}
    }

    .nav_icon {
      display: none;

    }
    .navbar > ul > li {
      white-space: nowrap;
      padding: 5px 0 5px 18px;
    }

    .navbar a, .navbar a:focus {
      display: block;
      align-items: center;
      justify-content: center;
      padding: 0 3px;
      font-size: 15px;
    }
    }
  }
