
h1, h2, h3, h4, h5, h6 {
  color: #222;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  margin-top: 0;
}

h1 {
  font-size: 60px;
  line-height: 72px;
}

h2 {
  font-size: 48px;
  line-height: 54px;
}

h3 {
  font-size: 34px;
}

h4 {
  font-size: 20px;
  line-height: 28px;
}

h5 {
  font-size: 18px;
  line-height: 22px;
}

h6 {
  font-size: 16px;
  line-height: 18px;
}


.product-content .price-box .new-price {
