}

.product-content .price-box .new-price {
  color: #ff5151;
}

.product-content .price-box .old-price {
  text-decoration: line-through;
}

.product-content {
  margin-top: 20px;
  text-align: center;

  h3 {
    font-size: 16px;
    font-weight: 500;
    margin: 0;
  }

  .price-box {
    font-size: 14px;

    & span {
      margin: 0 8px;
    }

    & .new-price {
      color: #FF5151FF;
    }

    & .old-price {
      text-decoration: line-through;
    }
  }
}

.text-left {
  text-align: left !important
}

.product-image .product-action .wishlist, .product-image .product-action .add-to-cart, .product-image .product-action .quick-view {
  background: #ddd none repeat scroll 0 0;
  display: inline-block;
  font-size: 18px;
  height: 40px;
  line-height: 44px;
  width: 40px;
  color: #222;
}

.product-image .product-action .wishlist:hover, .product-image .product-action .add-to-cart:hover, .product-image .product-action .quick-view:hover {
  background: #ff5151;
  color: #ffffff;
}

.product-image .product-action {
  display: inline-block;
  left: 0;
  position: absolute;
  text-align: center;
  top: 90%;
  width: 100%;
  opacity: 0;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.product-image span.label {
  position: absolute;
  top: 0;
  background: #ff5151;
  color: #ffffff;
  font-size: 12px;
  padding: 2px 15px;
  border-radius: 25px;
}

.footer-contact label {
  display: block;
  font-weight: 500;
  margin-bottom: 2px;
}

.account-input-box label {
  display: block;
  font-size: 15px;
  margin-bottom: 3px;
}

.coupon-input label {
  display: block;
  font-size: 14px;
}

.single-form-row label {
  font-size: 14px;
  margin-bottom: 2px;
}

.single-form-row label span.required {
  color: red;
}

.product-image {
  position: relative;

  & > a {
    display: block;
    position: relative;
    border-bottom: 1px solid #eeeeee;

    & img {
      width: 100%;
    }
  }

  & .product-action {
    display: inline-block;
    left: 0;
    position: absolute;
    text-align: center;
    top: 90%;
    width: 100%;

    & .wishlist,
    & .add-to-cart,
    & .quick-view {
      background: #ddd none repeat scroll 0 0;
      display: inline-block;
      font-size: 18px;
      height: 40px;
      line-height: 44px;
      width: 40px;
      color: #222;

      &:hover {
        background: #FF5151FF;
        color: white;
      }
    }
  }

  span.label {
    position: absolute;
    top: 0;
    background: #FF5151FF;
    color: white;
    font-size: 12px;
    padding: 2px 15px;
    border-radius: 25px;
  }
}

