.product-active-lg-4 {
  overflow: hidden;
}

.product-active-lg-4 .slick-arrow, .product-two-row-4 .slick-arrow {
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 15px;
  right: auto;
  background: #ddd;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 5px;
  z-index: 5;
  visibility: hidden;
  opacity: 0;
  transform: translateY(-50%);
  transition: 0.3s ease-in-out;
}

.product-active-lg-4 .slick-arrow.slick-next, .product-two-row-4 .slick-arrow.slick-next {
  right: 15px;
  left: auto;
}

.product-active-lg-4:hover .slick-arrow, .product-two-row-4:hover .slick-arrow {
  visibility: visible;
  opacity: 1;
}

.product-active-lg-4:hover .slick-arrow.slick-prev, .product-two-row-4:hover .slick-arrow.slick-prev {
  left: 0;
}

.product-active-lg-4:hover .slick-arrow.slick-next, .product-two-row-4:hover .slick-arrow.slick-next {
  right: 0;
}

.shop-sidebar-wrap {
  @media screen and (min-width: 768px) and (max-width: 991px) {
    margin-top: 80px;
  }
  @media only screen and (max-width: 767px) {
    margin-top: 70px;
  }
  @media only screen and (max-width: 479px) {
    margin-top: 60px;
  }
}

.product-layout-list {
  & p {
    margin-top: 20px;
  }

  &:hover .product-action {
    opacity: 1;
    visibility: visible;
    transform: translateY(-50%);
  }

  &:hover .product-image a::before {
    opacity: 1;
  }
}

.tab-content {
  width: 100%;
  display: block;
}

.tab-content .tab-pane {
  display: block;
  height: 0;
  max-width: 100%;
  visibility: hidden;
  overflow: hidden;
  opacity: 0;
}

.tab-content .tab-pane.active {
  height: auto;
  visibility: visible;
  opacity: 1;
  overflow: visible;
}
