
.loginnow p{
  font-size:16px;
  color:#757575;
}
.loginnow label a,
.loginnow p a{
  color:#e54c2a;
}
.loginnow h5{
  font-size:18px;
  font-weight:600;
  text-transform:uppercase;
  text-align:center;
  margin:10px 0 30px;
}
.loginto ul{
  margin:0;
  text-align:center;
}
.loginto ul li{
  width:100%;
  margin:0 0 30px;
  padding:10px 0;
  border-radius:4px;
}
.loginto ul li a{
  color:#fff;
  text-transform:uppercase;
  font-size:18px;
  font-weight:600;
}
.loginto ul li i{
  margin-right:15px;
  font-size:18px;
}
.loginto ul li:first-child{
  background: rgb(81, 91, 212);
}
.loginto ul li:nth-child(2){
  background: #1CB8EB;
}
.loginto ul li:last-child{
  background: blue;
}
.loginto ul li:nth-child(3){
  background: #E44120;
}
.loginto ul li:last-child(4){
  background: #E44120;
}
.loginnow{
  background: #f5f5f5;
  padding:50px;
  border:1px solid #e5e5e5;
  height:490px;
  margin-bottom: 50px;
  border-radius: 5px;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.15);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.15);
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.15);
}
.loginnow .form-group{
  margin-bottom:30px;
  position:relative;
}
.loginnow .form-group .icofont{
  position:absolute;
  right:0;
  padding:15px 12px;
  color:#e54c2a;
  top:0;
}
.loginnow .form-control{
  height:45px;
  color:#757575;
  padding-right: 35px;
}
.loginnow .form-control:focus{
  box-shadow: none;
  border-color: #e54c2a;
}
.loginnow .links{
  color:#757575;
  font-size:14px;
}
.loginnow .links .sign{
  color:#757575;
  font-weight:400;
}
.loginnow .links input{
  margin:0 5px 0 0;
}
.get-password{
  height: auto;
}

.me-login-close {
  font-size: 25px;
  position: absolute;
  top: -10px;
  right: -10px;
  background: #ffb628;
  width: 30px;
  height: 30px;
  display: block;
  border-radius: 1400%;
  line-height: 30px;
  color: #fff;
  text-align: center;
}
.me-login-form input {
  margin-bottom: 20px;
}
.me-login-title {
  margin-bottom: 20px;
  font-size: 36px;
  font-weight: 700;
  text-align: center;
}
.me-login-model .modal-body {
  padding: 41px 0px 0px;
}
.me-login-model form {
  margin-bottom: 0;
}
.me-login-form {
  padding: 0px 50px;
}
.me-remember input {
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
  margin-bottom: 0;
}
.me-remember .s_checkbox {
  position: absolute;
  top: 4px;
  left: 0;
  border: 1px solid rgb(233, 237, 250);
  border-radius: 4px;
  background-color: rgb(248, 249, 253);
  box-shadow: 0px 0px 40px 0px rgba(0, 0, 0, 0.04);
  width: 17px;
  height: 17px;
}
.me-remember {
  margin: 15px 0px 27px;
  position: relative;
}
.me-remember .s_checkbox:after {
  left: 6px;
  top: 2px;
  width: 5px;
  height: 10px;
  border: solid #f93e7e;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.me-remember input:checked ~ .s_checkbox:after {
  display: block;
}
.me-remember .s_checkbox:after {
  content: "";
  position: absolute;
  display: none;
}
.me-remember label {
  padding-left: 30px;
  cursor: pointer;
}
.me-login-btn{
  position: relative;
}
.me-forgot-password,.me-login-btn p{
  position: absolute;
  right: 0;
  top: 0;
}
.me-login-btn p {
  margin-bottom: 0px;
  bottom: 0;
  display: flex;
  align-items: center;
}
.me-login-btn p a{
  color: blue;
  padding-left: 5px;
  font-weight: 600;
}
.me-login-btn .me-btn{
  line-height: 39px;
}
.me-login-with-social {
  background: linear-gradient(45deg, #f93e7e, #ffb628);
  padding: 20px;
  margin-top: 50px;
  text-align: center;
  position: relative;
}
.me-login-with-social ul {
  padding: 0;
}
.me-login-with-social ul li {
  display: inline-block;
  margin-right: 5px;
}
.me-login-with-social ul li:last-child{
  display: none;
}
.me-login-with-social p {
  color: #fff;
  margin: 7px 0px 15px;
}
.me-login-with-social a,.me-login-with-social span {
  width: 30px;
  height: 30px;
  display: inline-block;
  background: #fff;
  border-radius: 100%;
  text-align: center;
  line-height: 30px;
  color: #f93e7e;
}
.me-login-with-social a i {
  line-height: 30px;
}
.me-login-with-social span {
  position: absolute;
  top: -17px;
  left: 0;
  right: 0;
  text-align: center;
  margin: auto;
  text-transform: uppercase;
  box-shadow: 0px 0px 27px 0px rgba(0, 0, 0, 0.12);
}
.me-login-model .modal-content{
  border: none;
  border-radius: 0;
}
.modal.me-login-model {
  overflow-y: auto;
}

.me-btn{
  display: inline-block;
  min-width: 130px;
  height: 40px;
  line-height: 40px;
  border: none;
  background: blue;
  color: #ffffff;
  outline: none;
  text-align: center;
  border-radius:30px;
  position: relative;
  transition: 0.3s;
  text-transform: capitalize;
  cursor: pointer;
}
.me-btn:focus{
  outline: none;
}
.me-btn:hover{
  background: #ffffff;
  color: #606060;
}
.me-btn:after{
  position: absolute;
  border: 2px solid #ffb628;
  content: '';
  width: 130px;
  height: 40px;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  border-radius: 30px;
  transform: scale(0);
  transition: 0.3s;
}
.me-btn:hover:after{
  transform: scale(1);
}