
.shop{
  margin-top: 8%;
}

.shopdetail {
  .image {
    border-radius: 4px;
    border: 1px solid #e5e5e5;
    overflow: hidden;
  }

  h2 {
    margin: 0 0 15px;
    font-size: 24px;
    color: #000;
    font-weight: 600;
    line-height: 26px;
  }

  .rating i {
    font-size: 18px;
    color: #FFA41F;
  }

  .rating i:last-child {
    color: #b1b1b1;
  }

  .price {
    font-size: 22px;
    color: #e54c2a;
    font-weight: 600;
    margin: 15px 0 10px;
    line-height: 18px;
  }

  .des {
    font-size: 13px;
    font-weight: 600;
    color: #000;
    margin: 8px 0 10px 0;
    line-height: 20px;
  }

  .shortdes {
    font-size: 14px;
    color: #757575;
    margin: 0 0 24px;
  }

  .food {
    margin-bottom: 15px;
  }

  .food h3 {
    font-size: 16px;
    font-weight: 600;
    color: #000;
    margin: 0 0 15px;
    text-transform: uppercase;
  }

  .food ul {
    margin: 0;
  }

  .food ul li .check input {
    height: auto;
    margin: 0 8px 0 0;
    vertical-align: middle;
  }

  .food ul li .check {
    font-size: 14px;
    font-weight: 400;
    color: #000;
    margin: 0px 0 10px;
  }

  .food ul li:last-child .check {
    margin: 0 0 0;
  }

  .nav-tabs {
    margin: 50px 0 30px;
  }

  .nav-tabs > li:hover a,
  .nav-tabs > li a.active {
    background: #e54c2a;
    color: #fff;
    border: none;
  }

  .nav-tabs > li a {
    color: #000;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 16px;
    line-height: 14px;
    border: none;
    padding: 20px 17px;
  }

  .tab-content p {
    color: #757575;
    font-size: 15px;
    line-height: 1.5;

  }

  #tab-review .box {
    border-bottom: 1px solid #e5e5e5;
    margin-bottom: 20px;
  }

  #tab-review .box:last-child {
    border-bottom: 0px solid #e5e5e5;
  }

  #tab-review .box img {
    border-radius: 4px;
    float: left;
    margin-right: 20px;
  }

  #tab-review .box .detail {
    margin: 0 0 16px 120px;
  }

  #tab-review .box .detail h2 {
    font-size: 16px;
    font-weight: 600;
    color: #000;
    margin: 0 0 2px;
  }

  #tab-review .box .detail span {
    font-size: 14px;
    color: #b2b2b2;
  }

  #tab-review .box .detail .rating {
    margin: 8px 0 10px;
  }

  #tab-review .box .detail p {
    font-size: 14px;
    margin: 0;
  }

  #form-review .form-group {
    margin-bottom: 0;
  }

  #form-review .form-control {
    min-height: 45px;
    box-shadow: none;
    margin: 0 0 30px;
    resize: none;
  }

  #form-review .form-control:focus {
    border-color: #e54c2a;
  }

  #form-review {
    .rating {
      margin-bottom: 30px;

      p {
        font-size: 14px;
        color: #000;
        text-transform: uppercase;
        margin: 0 0 10px;
      }

      i {
        color: #b2b2b2;
      }
    }
  }
}

.qtypara {
  width: 135px;
  margin: 0 5px;
  position: relative;

  .minus{
    position: absolute;
    left: 0;
    cursor: pointer;
    top: 5px;
  }

  .add {
    position: absolute;
    cursor: pointer;
    right: 0;
    top: 5px;

    .icofont {
      border-radius: 0 15px 15px 0;
    }

    .icofont {
      background-color: #e5e5e5;
      color: #A4A4A4;
      font-size: 14px;
      border-radius: 15px 0 0 15px;
      padding: 10px 10px;

      :hover {
        background-color: #e54c2a;
        color: #fff;
      }
    }

  }
  .form-control {
    height: 37px;
    text-align: center;
    border-radius: 40px;
    padding: 2px 50px;
    border-color: #e5e5e5;
    :focus {
      box-shadow: none;
      border-color: #e5e5e5;
    }
  }
}

.nav-tabs {
  display: flex;
  justify-content: end;
}

.form-controll {
  width: 100%;
  min-height: 250px;
  box-shadow: none;
  margin: 0 0 30px;
  resize: none;
  padding: 15px;
}

