
.paginatoin-area {

}
.page-pagination {
  background: #fff6f6;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  padding: 10px 20px;
  & a {
    color: #FF5151FF;
    &:hover {
      color: #222;
    }
  }
  & ul {
    display: flex;
    justify-content: center;
  }
  & li {
    font-size: 16px;
    line-height: 24px;
    font-family: 'Poppins', sans-serif;;
    color: #222222FF;
    text-align: center;
    margin: 2px 3px;
    & a {
      color: #333;
      padding: 10px;
      border-radius: 50px;
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      font-weight: 500;
    }
    // Responsive
    @media (max-width: 479px){
      font-size: 14px;
      & a {
        padding: 9px;
        width: 35px;
        height: 35px;
        & i {
          line-height: 24px;
        }
      }
    }
    &:hover {
      & a {
        color:  #FF5151FF;
        background-color: white;
        box-shadow: 0 0 10px rgba(0,0,0,0.5);
      }
    }
    &.active {
      & a {
        color: #FF5151FF;
        background-color: white;
        box-shadow: 0 0 10px rgba(0,0,0,.5);
      }
    }

  }
}

.pagination-box {
  display: flex;
  justify-content: center;
  margin-top: 40px;
}
@media only screen and (max-width: 767px) {
  .pagination-box {
    margin-top: 40px;
  }
}
.pagination-box li {
  display: inline-block;
  margin: 0 4px;
}
.pagination-box li.active a {
  color: #ffffff;
  background: #ff5151;
  border: 1px solid #ff5151;
}
.pagination-box li a {
  display: block;
  width: 40px;
  height: 40px;
  border: 1px solid #e8e8e8;
  line-height: 40px;
  text-align: center;
  color: #ff5151;
  font-size: 14px;
  border-radius: 5px;
}
.pagination-box li a:hover {
  color: #ffffff;
  background: #ff5151;
  border: 1px solid #ff5151;
}
.pagination-box li a span {
  font-size: 18px;
}
