.pro_review {
  display: flex;
}
.pro_review.ans {
  margin-bottom: 50px;
  margin-left: 50px;
  margin-top: 50px;
}
@media only screen and (max-width: 479px) {
  .pro_review.ans {
    margin-bottom: 30px;
    margin-left: 20px;
    margin-top: 30px;
  }
}
.review_info{
  a{
    text-decoration: none;
    color: black;
  }
  .review_thumb{
    border: black solid 1px;
  }
}

.pro_review:nth-child(3n) {
  padding-left: 120px;
}
@media only screen and (max-width: 767px) {
  .pro_review:nth-child(3n) {
    padding-left: 20px;
  }
}
@media only screen and (max-width: 479px) {
  .pro_review {
    margin-bottom: 20px;
  }
}