
.shop-top-bar {
  display: flex;
  justify-content: space-between;
  padding: 15px 15px;
  border: 1px solid #ddd;
  margin-bottom: 10px;
  align-items: center;
}

.shop-item-filter-list {
  position: 20px;
  & li {
    & a {
      font-size: 26px;
      font-weight: 500;
      margin-right: 20px;
      & .active {
        color: #FF5151FF;
      }
    }
    & .active {
      color:#FF5151FF;
    }
  }
}

.product-short {
  display: flex;
  font-size: 14px;
}
.product-short p {
  margin: 0 15px 0 0;
}
.product-short .nice-select {
  border: 1px solid #ddd;
  padding: 0 8px;
  height: 30px;
}

.nice-select,.input-box-2 {
  width: 100%;
  height: 45px;
}
.nice-select .list {
  min-width: 100%;
  background-color: #f1f1ff;
  border: 1px solid #f1f1ff;
  box-shadow: none;
}