.section-title {
  margin-bottom: 20px;
}
@media only screen and (max-width: 767px) {
  .section-title {
    margin-bottom: 10px;
  }
}
.section-title h2 {
  font-size: 32px;
  font-weight: 700;
  margin: -5px 0 0 0;
  line-height: 1.6;
}
@media only screen and (max-width: 479px) {
  .section-title h2 {
    font-size: 26px;
    line-height: 30px;
  }
}
.section-title h2 span {
  color: #ff5151;
}
.section-title p {
  max-width: 640px;
  margin-top: 16px;
  margin-bottom: 0;
}
.section-title.text-center p {
  margin-left: auto;
  margin-right: auto;
}
.section-title.text-right p {
  margin-left: auto;
  margin-right: 0;
}

.title-2 h4 {
  color: #ff5151;
  font-weight: 600;
}
.title-2 h3 {
  color: #ff5151;
  font-weight: 600;
}
