@import url('https://fonts.googleapis.com/css2?family=Asap+Condensed&family=Righteous&display=swap');

.AboutCom {
  margin: 10% 0 10% 0;

  .about_h {
    text-align: center;
    font-family: 'Righteous', cursive;
    text-shadow: 1px 0 1px #CCCCCC, 0 1px 1px #EEEEEE, 2px 1px 1px #CCCCCC, 1px 2px 1px #EEEEEE, 3px 2px 1px #CCCCCC, 2px 3px 1px #EEEEEE, 4px 3px 1px #CCCCCC, 3px 4px 1px #EEEEEE, 5px 4px 1px #CCCCCC, 4px 5px 1px #EEEEEE, 6px 5px 1px #CCCCCC, 5px 6px 1px #EEEEEE, 7px 6px 1px #CCCCCC;
  }

  .about_img_div {
    margin: 2%;
  }

  .about_img2 {
    width: 600px;
    height: 500px;
    margin: auto;
    box-shadow: 1px 5px 15px 1px #000000;
    border: 5px solid #f0f0f0;
    border-radius: 35px 0 35px 0;
  }

  .about_text {
    margin: auto;
    width: 600px;

    .about_h1 {
      font-size: 50px;
      text-align: start;
      font-family: 'Righteous', cursive;
    }

    .about_p {
      font-size: 19px;
      font-family: 'Asap Condensed', sans-serif;

    }
  }

  @media (max-width: 1200px) and (min-width: 1024px) {
    .about_img_div {
      margin: auto;
    }

    .about_img2 {
      width: 350px;
      height: 250px;

    }

    .about_text {
      margin: auto;
      width: 450px;

      .about_h1 {
        margin-top: 7%;
        font-size: 35px;
        text-align: center;
      }

      .about_p {
        font-size: 15px;
        font-family: 'Asap Condensed', sans-serif;

      }
    }
  }
  @media (max-width: 1024px) and (min-width: 768px) {
    .about_img_div {
      margin: auto;
    }

    .about_img2 {
      width: 350px;
      height: 250px;
      margin: 0 0 0 23%;
    }

    .about_text {
      margin: auto;
      width: 450px;

      .about_h1 {
        margin-top: 7%;
        font-size: 30px;
        text-align: center;
      }

      .about_p {
        font-size: 14px;
        font-family: 'Asap Condensed', sans-serif;

      }
    }
  }
  @media (max-width: 767px) and (min-width: 357px) {

    .about_img2 {
      width: 250px;
      height: 180px;
      margin: 0 0 0 23%;
    }

    .about_text {
      width: 300px;

      .about_h1 {
        margin-top: 7%;
        font-size: 25px;
        text-align: center;
      }

      .about_p {
        font-size: 14px;
        font-family: 'Asap Condensed', sans-serif;

      }
    }
  }
}
