@import url('https://fonts.googleapis.com/css2?family=Gemunu+Libre:wght@700&display=swap');

.CarouselCom {
  width: 100%;
  margin-top: 2%;
  background: rgb(63, 94, 251);
  background: radial-gradient(circle, rgba(63, 94, 251, 1) 0%, rgba(252, 70, 107, 1) 100%);

  .carousel_div {
    position: absolute !important;
    top: 35%;
    left: 15%;

    .carousel_h3 {
      width: 530px;
      text-align: start;
      font-size: 75px;
      color: #ffff;
      font-family: 'Gemunu Libre', sans-serif;

      b {
        color: darkorange;
      }
    }

    .carousel_p {
      font-size: 30px;
      text-align: start;
    }
  }

  .carousel_div1 {
    position: absolute ;
    top: 35% ;
    left: 55% ;

    .carousel_h3 {
      width: 530px;
      text-align: start;
      font-size: 75px;
      color: #ffff;
      font-family: 'Gemunu Libre', sans-serif;

      b {
        color: darkorange;
      }
    }

    .carousel_p {
      font-size: 30px;
      text-align: start;
    }
  }


  @media (max-width: 1200px) and (min-width: 1024px) {
    margin-top: 4%;

    .carousel_div {
      position: absolute !important;
      top: 35%;
      left: 15%;

      .carousel_h3 {
        width: 350px;
        font-size: 48px;
      }

      .carousel_p {
        font-size: 20px;
      }
    }

    .carousel_div1 {
      position: absolute !important;
      top: 25% ;
      right: 15% ;

      .carousel_h3 {
        width: 350px;
        font-size: 48px;
      }

      .carousel_p {
        font-size: 20px;
      }
    }
  }

  @media (max-width: 1024px) and (min-width: 768px) {
    margin-top: 2%;

    .carousel_div {
      position: absolute !important;
      top: 32%;
      left: 12%;

      .carousel_h3 {
        width: 280px;
        font-size: 38px;
      }

      .carousel_p {
        font-size: 15px;
      }
    }

    .carousel_div1 {
      position: absolute !important;
      top: 32% ;
      right: 12% ;

      .carousel_h3 {
        width: 280px;
        font-size: 38px;
      }

      .carousel_p {
        font-size: 15px;
      }
    }
  }

  @media (max-width: 767px) and (min-width: 357px) {
    margin-top: 12%;

    .carousel_div {
      position: absolute !important;
      top: 21%;
      left: 6%;

      .carousel_h3 {
        width: 150px;
        font-size: 21px;
      }

      .carousel_p {
        font-size: 10px;
      }
    }

    .carousel_div1 {
      position: absolute !important;
      top: 18% ;
      right: 6% ;

      .carousel_h3 {
        width: 150px;
        font-size: 21px;
      }

      .carousel_p {
        font-size: 10px;
      }
    }
  }
}