@import url("https://fonts.googleapis.com/css?family=Montserrat:200,300,400,500,600,700,800|Poppins:300,400,500,600,700,800,900");

// Font Family
$body-font:  'Poppins', sans-serif;
$heading-font: 'Poppins', sans-serif;




// Colors ---------------

$white: #ffffff;
$black: #000000;
$grey: #fafafa;

// Body Color
$body-color: #222;

// footer Color
$footer-color: #222;

// Heading Color
$heading-color: #222;

// Theme Color
$theme-color: #ff5151;
$theme-color-2: #19cfa3;
$theme-color-3: #f1f1ff;



// Responsive Variables
$extraBig-device : 'only screen and (min-width: 1600px)';
$laptop-device : 'only screen and (min-width: 1200px) and (max-width: 1599px)';
$desktop-device : 'only screen and (min-width: 992px) and (max-width: 1199px)';
$tablet-device:'only screen and (min-width: 768px) and (max-width: 991px)';
$large-mobile:'only screen and (max-width: 767px)';
$small-mobile: 'only screen and (max-width: 479px)';



/* transition */
@mixin transition($property){
  -webkit-transition: $property;
  transition: $property;
}

/* flex */
@mixin flex($property){
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  align-items: center
}

/* transform */
@mixin transform($property) {
  -webkit-transform: $property;
  -ms-transform: $property;
  transform: $property;
}


/* opacity */
@mixin opacity($opacity){
  opacity: $opacity;
  -ms-filter: $opacity;
}

